import {
  useHistory
} from "react-router-dom";
import * as backend from '../build/index.main.mjs'
import Button from 'react-bootstrap/Button'
import { useState } from "react";
import { Col, Row } from "react-bootstrap";

const Sell = (props) => {
  const { acc, stdlib } = props
  const [state, setState] = useState({
    success: false
  })
  const [query, setQuery] = useState({})
  const handleSubmit = async () => {
    if (!query.ASSETID
      || !query.STARTBID
      || !query.DEADLINEDATE) {
      alert("Enter Asset id, Start bid, and Deadline date to submit")
      return
    }
    console.log("Launching new game ...")
    const ctcAuction = acc.deploy(backend)
    const ctcAuctionInfo = await ctcAuction.getInfo()
    await Promise.all([
      backend.Auctioneer(ctcAuction, {
        getParams: () => new Promise((resolve) => {
          resolve({
            addr: '7GDBWS6G5YB5TS4SYTY5IRKHYR5G7JZIAAIGNNSV3VFQJ7MOA3EZZ3QLFI',
            token: parseInt(query.ASSETID), //24270812, // ShitCoin
            reservePrice: stdlib.parseCurrency(100), // 15 ALGO
            deadline: 100, // x blocks
            maxDeadline: 100, // y blocks, where y > x
            unitAmount: stdlib.parseCurrency(1),
            deadlineStep: 100
          })
        }),
        signal: () => setState({...state, success: true, appId: ctcAuctionInfo}), 
        ...stdlib.hasConsoleLogger,
      }),
    ])
  }
  const handleChange = ({ target }) => {
    const { name, value } = target
    setQuery({
      ...query,
      [name]: value
    })
  }
  /*
  const handleSubmit = () => {
    if (query.ASSETID
      && query.STARTBID
      && query.DEADLINEDATE)
      setState({
        ...state,
        success: true
      })
    else
      alert("Enter Asset id, Start bid, and Deadline date to submit")
  }
  */
  const handleSellOneMore = () => {
    setState({
      success: false
    })
    setQuery({})
  }
  let history = useHistory();
  const style = {
    welcome: {
      fontFamily: 'Roboto',
      fontSize: '20px',
      fontStyle: 'normal',
      fontWeight: '300',
      lineHeight: '23px',
      letterSpacing: '0.1em',
      textAlign: 'center'
    },
    serviceName: {
      fontFamily: 'Roboto',
      fontSize: '32px',
      fontStyle: 'normal',
      fontWeight: '900',
      lineHeight: '38px',
      letterSpacing: '0.1em',
      textAlign: 'center',
      textTransform: 'uppercase',
      marginTop: '4px'
    },
    serviceDescription: {
      fontFamily: 'Roboto',
      fontSize: '36px',
      fontStyle: 'normal',
      fontWeight: '300',
      lineHeight: '42px',
      letterSpacing: '0em',
      textAlign: 'center',
      textTransform: 'uppercase',
      marginTop: '20px'
    }
  }
  const Success = () => {
    const labelStyle = {
      "fontFamily": "Roboto",
      "fontStyle": "normal",
      "fontWeight": "normal",
      "fontSize": "14px",
      "lineHeight": "16px",
      "color": "#000000",
      "opacity": "0.5",
      "padding": "0px"
    }
    const valueStyle = {
      "fontFamily": "Roboto",
      "fontStyle": "normal",
      "fontWeight": "500",
      "fontSize": "14px",
      "lineHeight": "16px",
      "color": "#000000",
      "marginTop": "6px"
    }
    return <>
      <div className="mt-2" style={style.serviceName}>Success</div>
      <div className="jam success" style={{ margin: '24px auto' }}>
        <img src="https://via.placeholder.com/254" alt="NFT" />
        <Row style={{ marginTop: '22px' }}>
          {[
            {
              label: "Asset id",
              value: query.ASSETID
            },
            {
              label: "Start bid",
              value: `${query.STARTBID} Algo`
            },
            {
              label: "Deadline date",
              value: query.DEADLINEDATE
            }
          ].map(el => <Col style={{ padding: '0px', height: '38px', overflow: 'hidden' }}>
            <Row>
              <Col xs={12} className="text-left" style={labelStyle}>{el.label}</Col>
              <Col xs={12} style={valueStyle}>{el.value}</Col>
            </Row>
          </Col>)}
        </Row>
        <Button className="btn-submit" style={{ marginTop: "42px" }} onClick={()=>history.push(`/auction/${state.appId}`)}>OK</Button>
        <div className="btn-cancel" style={{ marginTop: "32px" }} onClick={handleSellOneMore}>Sell One More NFT</div>
      </div>
    </>
  }

  return <div id="sell" className="mt-5">
    {!state.success ? <>
      <div className="mt-2" style={style.serviceName}>Sell Your NFT</div>
      <div className="jam default" style={{ margin: '24px auto' }}>
        {[
          {
            name: "ASSETID",
            label: "Asset id",
            placeholder: "Enter 7 digits number. Example : 9381029"
          },
          {
            name: "STARTBID",
            label: "Start bid",
            placeholder: "Enter price in Algorand",
            style: { marginTop: "14px" }
          },
          {
            name: "DEADLINEDATE",
            label: "Deadline date",
            placeholder: "Select date when the auction will be finished",
            style: { marginTop: "14px" }
          },
        ].map((el, i) => <div key={`sell-input-${i}`} style={el.style}>
          <label for={el.name}>{el.label}</label>
          <input className="border-0" name={el.name} defaultValue={query[el.name]} placeholder={el.placeholder} onChange={handleChange} />
        </div>)}
        <Button className="btn-submit" style={{ marginTop: "42px" }} onClick={handleSubmit}>Submit</Button>
        <div className="btn-cancel" style={{ marginTop: "32px" }} onClick={() => history.push("/")}>Cancel</div>
      </div>
    </> : <Success />}
  </div>
}

export default Sell;