import {
  useHistory,
  useLocation
} from "react-router-dom";
import wallet from '../wallet.svg';
import logo from '../logo.svg';
import Container from 'react-bootstrap/Container'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'

const NFTJamNavbar = (props) => {
  let { acc } = props
  let location = useLocation()
  let history = useHistory()
  const navbarBrandNameStyle = {
    lineHeight: '62px',
    fontWeight: 'bold',
    marginLeft: '8px'
  }
  return <Navbar bg="light" expand="lg">
    <Container>
      <Navbar.Brand onClick={()=>history.push("/")}>
        <img
          src={logo}
          width="62"
          height="62"
          className="d-inline-block align-top rounded"
          alt="Logo"
        />
        <div className="d-inline-block" style={navbarBrandNameStyle}>
          NFT Jam
        </div>
      </Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav" style={{ justifyContent: "flex-end" }}>
        <Nav style={{ columnGap: '2em', fontWeight: '500' }}>
          {[
            {
              path: "/sell",
              text: "Sell NFT"
            },
            {
              path: "/auction",
              text: "Auction"
            },
            {
              path: "/faq",
              text: "FAQ"
            },
          ].map(el =>
            <Nav.Link className={location.pathname.indexOf(el.path) !== -1 && "active"} onClick={()=>history.push(el.path)}>{el.text}</Nav.Link>
          )}
          {acc ? (
            <Nav.Link><span style={{ marginLeft: '8px' }}>Connect as {(acc?.networkAccount?.addr).substr(0,8)+"..."}</span></Nav.Link>
          ) : (
            <Nav.Link href="/"><img src={wallet} style={{ filter: 'opacity(0.5)' }} alt="Algorand Wallet Icon" width="30" height="26"/><span style={{ marginLeft: '8px' }}>Connect wallet</span></Nav.Link>
          )}
        </Nav>
      </Navbar.Collapse>
    </Container>
  </Navbar>
}

export default NFTJamNavbar;
