import React, { useEffect, useState } from "react";
import { loadStdlib } from '@reach-sh/stdlib'
import MyAlgoConnect from '@reach-sh/stdlib/ALGO_MyAlgoConnect';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import Home from './pages/Home'
import Sell from './pages/Sell'
import Auction from './pages/Auction'
import Navbar from './components/Navbar'
import Container from 'react-bootstrap/Container'
import './App.css';


const { REACT_APP_NETWORK_PROVIDER, REACT_APP_NETWORK } = process.env
const stdlib = loadStdlib(REACT_APP_NETWORK)
if (REACT_APP_NETWORK === 'ALGO') {
  stdlib.setWalletFallback(stdlib.walletFallback({
    providerEnv: "TestNet", MyAlgoConnect
  }));
}

const Faq = () => <div>
  Under Construction ...</div>

const Connect = () => <div>
  Connect</div>

function App() {
  const [state, setState] = useState({
    acc: false,
    stdlib
  })
  const getAccount = async () => {
    /*
    let acc = localStorage.getItem('acc')
    console.log({acc})
    if(!acc) {
      acc = JSON.stringify(await stdlib.getDefaultAccount())
      localStorage.setItem('acc', acc)
    }
    setState({...state, acc: JSON.parse(acc)})
    */
    setState({...state, acc: await stdlib.getDefaultAccount()})
  }
  useEffect(() => {
    if(!state.acc) {
      getAccount()
    }
  })
  return (
    <Router>
      <div className="App">
        <Navbar {...state} />
        <Container
        /*
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            height: '100%',
            width: '100%'
          }}
          */
          className="mt-5"
          size="lg"
          fluid="lg">
          <Switch>
            <Route exact path="/auction">
              <Auction {...state} />
            </Route>
            <Route path="/auction/:appId">
              <Auction {...state} />
            </Route>
            <Route path="/sell">
              <Sell {...state} />
            </Route>
            <Route path="/faq">
              <Faq />
            </Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </Container>
      </div>
    </Router>
  );
}

export default App;